import React,{useContext, useState,useEffect} from 'react';
import {NavLink} from 'react-router-dom';
import NavBar from '../components/NavBar';
// import Poster from '../images/poster.jpg';
import AuthContext from '../store/auth-context';


const ToLetHouse=()=>{
const ctx = useContext(AuthContext);
// const navigate = useNavigate();



    const [roomType, setRomeType] = useState('null');
    const [priceMin, setPriceMin] = useState(null);
    const [priceMax, setPriceMax] = useState(null);
    const [district, setDistrict] = useState('null');
    const [municipality, setMunicipality] = useState('null');
    const [ward, setWard] = useState('null');
    const [path, setPath] = useState('null');
    const [resData,setResData] = useState([]);

    const [showRentListings,setShowRentListings]=useState(false);
    
    useEffect(() => {
      if(ctx.RentQuery){
      fetch(ctx.RentQuery)
    .then(response => response.json())
    
    .then((res) => {
        setResData(res.data.rent);
        console.log(res.data.rent);
    })
    setShowRentListings(true);
    
    }
    
    
     
    },[]);

    const handleRoomType =(e)=>{
      setRomeType(e.target.value);
    }
    const handlePriceMin =(e)=>{
      setPriceMin(e.target.value);
    }
    const handlePriceMax = (e)=>{
        setPriceMax(e.target.value);
    }
 
    const handleDistrict =(e)=>{
      setDistrict(e.target.value);
    }
    const handleMunicipality =(e)=>{
      setMunicipality(e.target.value);
    }
    const handleWard =(e)=>{
      setWard(e.target.value);
    }
    const handlePath =(e)=>{
      setPath(e.target.value);
    }
    
        
  
  
    
    
    
    
    
    
    
    
        const formHandler = async(e)=>{
        e.preventDefault();
    
       
let RentQuery="https://noagent4.onrender.com/rent?";
if( district!=="null" && municipality!=="null" && roomType!=="null"){
  if(roomType!=="null"){
RentQuery = RentQuery+"room_type="+roomType;
  }
  if(priceMin){
    RentQuery = RentQuery+"&price[gte]="+priceMin;
  }else{
    RentQuery = RentQuery+"&price[gte]="+0;

  }
  if(!isNaN(priceMax) && !priceMax===0){
    RentQuery = RentQuery+"&price[lte]="+priceMax;
  }
  if(district!=="null"){
    RentQuery = RentQuery+"&district="+district;
  }
  if(municipality!=="null"){
    RentQuery = RentQuery+"&municipality="+municipality;
  }
  if(ward!=="null"){
    RentQuery = RentQuery+"&ward="+ward;
  }
  if(path!=="null"){
    RentQuery = RentQuery+"&path="+path;
  }


  fetch(RentQuery)
.then(response => response.json())

.then((res) => {
    setResData(res.data.rent);
    console.log(res.data.rent);
})
ctx.RentQuery = RentQuery;
setShowRentListings(true);




}






}
    










  return (
<>
        <NavBar/>
        

      {!showRentListings &&  <div class="flex justify-center items-center px-3  mt-[100px]">
                <div class="rounded-lg shadow-xl bg-gray-50 md:w-1/3 w-[360px]">



                <div class="flex-col justify-center mt-[50px]">
 <form class="w-full max-w-lg" onSubmit={formHandler}>
  
  
  
  <div class="flex flex-wrap -mx-3 mb-6">
  
  
  
  
    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
        Room Type
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handleRoomType} value={roomType}>
        <option value="null">--Select--</option>
          
          <option value="single room">Single Room</option>
          <option value="multiple room">Multiple Rooms</option>
          <option value="flat">Flat</option>
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>
   






  </div>




  <div class="flex justify-start mb-8">
    <input type="number" placeholder='min price' class="w-2/5 mr-4 h-10 border " onChange={handlePriceMin} value={priceMin}/> 
    <h5>to</h5>
    <input type="number" placeholder='max price' class="w-2/5 ml-4 h-10 border" onChange={handlePriceMax} value={priceMax}/> 

  </div>
  


  <div class="flex flex-wrap -mx-3 mb-2">

 





    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
        District
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handleDistrict} value = {district}>
        <option value="null">--Select--</option>
          
          <option value="rupandehi">Rupandehi</option>
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>

    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state" >
        Municipality
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handleMunicipality} value={municipality}>
        <option value="null">--Select--</option>
          
          <option value="butwal">Butwal Municipality</option>
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>

    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state" >
        Ward
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handleWard} value={ward}>
        <option value="null">--Select--</option>
        
          <option value="devinagar">Devinagar</option>
          <option value="kalikanagar">KalikaNagar</option>
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>

    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
        Path
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handlePath} value={path}>
        <option value="null">--Select--</option>
        
          <option value="suwarna path">Suwarna Path</option>
          <option value="horizon chowk">Horizon Chowk</option>
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>




  </div>


  <div class="flex-row justify-center pb-8">
  <button type='submit' class="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded mt-2 lg:ml-[45%] md:ml-[40%]">Submit</button>
 </div>

 </form>
 </div>








                </div>
        </div>
 }



{showRentListings &&
 <>
    <div class="flex justify-center">
    <button class="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded mt-10" onClick={()=>{setShowRentListings(false)}}>
    Filter
  </button>
  </div>
 


   
  {resData.length>=1 && <p class="text-center mt-6 font-bold text-xl md:text-3xl">View Rents</p>}


  {!resData.length>=1 && <div class="text-center mt-40">No Rent Listings Matched</div>}


  <div class="flex flex-col mt-2 justify-center items-center md:flex-wrap md:space-x-6 space-y-4 md:flex-row ">

  {resData.map(data=>{
    return( 
  
    <div class="w-80 md:mt-4 border-4  border-blue-500 rounded-xl overflow-hidden hover:drop-shadow-lg">
       
<NavLink to={'/to-let-house/rent-details/'+data._id}>

       { data.productImage && <div class="h-40 ">
          {data.productImage?<img src={data.productImage[0]} class="h-[100%] w-[100%] object-cover" alt="listing"/>:null}
        </div>
  }
        <div class="textcontent mx-2 my-3 flex flex-col space-y-2">
        <p class="px-3 py-1 inline bg-gray-400 rounded-md">{data.room_type} </p>
       
        <p class=" ">{data.description}</p>
        <p class=" px-3 py-1 bg-blue-200 rounded-md">{data.district}-{data.municipality}-{data.ward}-{data.path} </p>
        <p class="px-3 py-1 inline bg-gray-400 rounded-md">Nrs:-{data.price} </p>
       
      </div>
</NavLink>

    </div>
  
    )      
})
}
</div>

</>
}

</>






    
  )
}

export default ToLetHouse;