import React,{useState,useContext,useEffect} from 'react';
import NavBar from '../components/NavBar';
import axios from 'axios';
import AuthContext from '../store/auth-context';
import { useNavigate } from 'react-router-dom';
import useGeoLocation from "../Hooks/useGeoLocation";


const RentYourHouse = () => {

  const location = useGeoLocation();


const ctx = useContext(AuthContext);
const navigate = useNavigate();
useEffect(() => {
  if(!ctx.NoAgentToken){
    navigate('/');
  }
   
  },[]);

    const [files, setFile] = useState([]);
    const [message, setMessage] = useState();
    const handleFile = (e) => {
        setMessage("");
        let file = e.target.files;

        for (let i = 0; i < file.length; i++) {
            const fileType = file[i]['type'];
            const validImageTypes = ['image/gif', 'image/jpeg', 'image/png'];
            if (validImageTypes.includes(fileType)) {
                setFile([...files, file[i]]);
            } else {
                setMessage("only images accepted");
            }

        }
    }; 

    const removeImage = (i) => {
        setFile(files.filter(x => x.name !== i));
     }

const [roomType, setRomeType] = useState('single room');
const [price, setPrice] = useState(null);
const [description, setDescription] = useState(null);
const [district, setDistrict] = useState('rupandehi');
const [municipality, setMunicipality] = useState('butwal');
const [ward, setWard] = useState('devinagar');
const [path, setPath] = useState('suwarna path');
const [coordinates,setCoordinates] = useState({});


const handleRoomType =(e)=>{
  setRomeType(e.target.value);
}
const handlePrice =(e)=>{
  setPrice(e.target.value);
}
const handleDescription =(e)=>{
  setDescription(e.target.value);
}
const handleDistrict =(e)=>{
  setDistrict(e.target.value);
}
const handleMunicipality =(e)=>{
  setMunicipality(e.target.value);
}
const handleWard =(e)=>{
  setWard(e.target.value);
}
const handlePath =(e)=>{
  setPath(e.target.value);
}


const getLocation = (e)=>{

e.preventDefault();
  setCoordinates(
    {
      "lat":location.coordinates.lat,
      "lng":location.coordinates.lng
    }
  )
}
    
// function handleAddrTypeChange(e) {
//   setAddrType(e.target.value);
//   console.log(addrtype)
// }
  
// <select
//   defaultValue={addrtype}
//   onChange={handleAddrTypeChange}
//   className="browser-default custom-select">
    
//   <option selected value="1">Home</option>
//   <option value="2">Marketing</option>
//   <option value="3">Work</option>
//   <option value="3">Head Office</option>

// </select> 








    const formHandler = async(e)=>{
    e.preventDefault();

    const formData = new FormData();
    files.forEach(file=>{
    formData.append("productImage", file)
    })
    formData.append("user_id",ctx.userId);
    formData.append("room_type",roomType)
    formData.append("price",price)
    formData.append("description",description)
    formData.append("district",district)
    formData.append("municipality",municipality)
    formData.append("ward",ward)
    formData.append("path",path)
    formData.append("lat",coordinates.lat*1)
    formData.append("lng",coordinates.lng*1)



    // {'Content-Type': 'multipart/form-data'},

    const result = await axios.post('https://noagent4.onrender.com/rent', formData, { headers: {'Authorization': ctx.NoAgentToken}  })

   console.log(result);
    if(result){
  
      navigate('/my-rent-listings');
    }


  
  }
    return (
        <>
        <NavBar/>
            <div class="flex justify-center  items-center px-3  mt-[11px]">
                <div class="rounded-lg shadow-xl bg-gray-50 md:w-1/2 w-[360px]">
                    <div class="m-4">
                        <span className="flex justify-center items-center text-[12px] mb-1 text-red-500">{message}</span>
                        <div class="flex items-center justify-center w-full">
                            <label class="flex cursor-pointer flex-col w-full h-32 border-2 rounded-md border-dashed hover:bg-gray-100 hover:border-gray-300">
                                <div class="flex flex-col items-center justify-center pt-7">
                                    <svg xmlns="http://www.w3.org/2000/svg"
                                        class="w-12 h-12 text-gray-400 group-hover:text-gray-600" viewBox="0 0 20 20"
                                        fill="currentColor">
                                        <path fill-rule="evenodd"
                                            d="M4 3a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V5a2 2 0 00-2-2H4zm12 12H4l4-8 3 6 2-4 3 6z"
                                            clip-rule="evenodd" />
                                    </svg>
                                    <p class="pt-1 text-sm tracking-wider text-gray-400 group-hover:text-gray-600">
                                        Select a photo</p>
                                </div>
                                <input type="file" onChange={handleFile} class="opacity-0" multiple="multiple" name="files[]" />
                            </label>
                        </div>
                        <div className="flex flex-wrap gap-2 mt-2">

                            {files.map((file, key) => {
                                return (
                                    <div key={key} className="overflow-hidden relative">
                                        <i onClick={() => { removeImage(file.name) }} className="mdi mdi-close absolute right-1 hover:text-white cursor-pointer"></i>
                                        <img className="h-20 w-20 rounded-md" src={URL.createObjectURL(file)} alt="" />
                                    </div>
                                )
                            })}



                        </div>



                    </div>
<div class="flex-col justify-center mt-[50px]">
<form class="w-full max-w-lg" onSubmit={formHandler}>
  
  
  
  <div class="flex flex-wrap -mx-3 mb-6">
  
  
  
  
    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
        Room Type
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handleRoomType}>
          <option value="single room">Single Room</option>
          <option value="multiple room">Multiple Rooms</option>
          <option value="flat">Flat</option>
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>
    {/* <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
        State
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state">
          <option>New Mexico</option>
          <option>Missouri</option>
          <option>Texas</option>
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div> */}
  </div>






  <div class="flex flex-wrap -mx-3 mb-6">
    <div class="w-full px-3">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-password">
        Price
      </label>
      <input class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-password" type="number" placeholder="" onChange={handlePrice} />
      {/* <p class="text-gray-600 text-xs italic">Make it as long and as crazy as you'd like</p> */}
    </div>
  </div>






  <div class="flex flex-wrap -mx-3 mb-6">
    <div class="w-full px-3">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-password">
        Description
      </label>
      <textarea name="Text1" cols="40" rows="5" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 h-20 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-password" onChange={handleDescription}></textarea>
      <p class="text-gray-600 text-xs italic">You can describe here on your own.</p>
      
    </div>
  </div>


  <div class="flex flex-wrap -mx-3 mb-2">
    {/* <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-city">
        District
      </label>
      <input class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-city" type="text" placeholder="Albuquerque" />
    </div> */}
    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
        District
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handleDistrict}>
          <option value="rupandehi">Rupandehi</option>
          {/* <option>Dang</option>
          <option>Kapilbastu</option> */}
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>

    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
        Municipality
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handleMunicipality}>
          <option value="butwal">Butwal Municipality</option>
          {/* <option>Tilottama Municipality</option>
          <option>Sainamaina Municipality</option> */}
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>

    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
        Ward
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handleWard}>
          <option value="devinagar">Devinagar</option>
          <option value="kalikanagar">KalikaNagar</option>
          {/* <option></option> */}
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>

    <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
        Path
      </label>
      <div class="relative">
        <select class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state" onChange={handlePath}>
          <option value="suwarna path">Suwarna Path</option>
          <option value="horizon chowk">Horizon Chowk</option>
          {/* <option></option> */}
        </select>
        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
        </div>
      </div>
    </div>


   

  </div>


  <div className="inline-block mr-auto pt-1">
    <button onClick={getLocation} class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded">GetYourLocation</button>

                             
  </div>
  <div>
  {coordinates.lat?"lat:"+coordinates.lat+"   lng:"+coordinates.lng:''}
  </div>
    

  <div class="flex-row justify-center pb-8">
  <button type='submit' class="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded mt-2 lg:ml-[60%] md:ml-[40%]">Submit</button>
</div>

</form>
</div>

                </div>




            </div>

  






        </>
    );
}


export default RentYourHouse;