import React,{useEffect, useState,useContext} from 'react';
import { useParams } from 'react-router-dom';
import NavBar from '../components/NavBar';
import AuthContext from '../store/auth-context';
import { useNavigate } from 'react-router-dom';
import GoogleMapReact from "google-map-react";
import MyMarker from "../components/MyMarker";


const MyHomeListingItem=()=>{

    const { homeId } = useParams();
    const [resData,setResData]=useState([]);

// const [images,setImages]=useState([]);

    const ctx = useContext(AuthContext);
    const navigate = useNavigate();
    useEffect(() => {
 
      
            if(!ctx.NoAgentToken){
              navigate('/');
            }
             
       

        fetch('https://noagent3.onrender.com/home/'+homeId)
        .then(response => response.json())
        
        .then((res) => {
            setResData(res.product);
        })

   
    
       
},[]);


const DelHome = async ()=>{
  const requestOptions = {
    method: 'DELETE',
    headers: { 
        'Authorization': ctx.NoAgentToken
    }
};
const response = await fetch('https://noagent4.onrender.com/home/'+homeId, requestOptions);
const data = await response.json();
if(data){
  navigate('/my-home-listings')
}
}


  return (
   <div>
    <NavBar />
    <div class="flex flex-col justify-center items-center mt-20">
    <button class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded mb-4 " onClick={DelHome}>
  Delete
</button>
<div class="flex justify-center items-center flex-wrap border-4 shadow-2xl w-4/5">
{resData.productImage?.map(img=>
  (<img src={img} class="h-1/2 w-1/2 p-1 " alt=""/>)

)}
  


</div>
</div>

<div class="flex justify-center items-center">

<div class="mt-12 shadow-2xl border-4 pb-20 flex flex-col justify-center items-center w-4/5" >
<div class="text-xl font-bold border-b-2 border-blue-400">{resData.home_type}</div>

<div class="text-xl font-bold border-b-2 border-blue-400">Nrs.{resData.price}</div>

<div class="text-xl font-bold border-b-2 border-blue-400">{resData.district}-{resData.municipality}</div>
<div class="text-xl font-bold border-b-2 border-blue-400">{resData.ward}-{resData.path}</div>
<div class="text-xl font-bold border-b-2 border-blue-400">{resData.description}</div>


</div>
</div>






<div class="flex flex-col justify-center items-center mt-8 mb-8 h-100 overflow-hidden">
<div class=" w-4/5 h-[300px]">


<GoogleMapReact
        bootstrapURLKeys={{
          // remove the key if you want to fork
          key: "AIzaSyDSHxdIc8Tv1jc-7rPwympmuuutpCYDzzU",
          language: "en",
        }}
        defaultCenter={{ lat:27.6866, lng:83.4323 }}
        defaultZoom={15}
        // distanceToMouse={distanceToMouse}
      >
       
            <MyMarker key={1} lat={resData.lat} lng={resData.lng} text={1} tooltip={'butwal'} />
         
        
      </GoogleMapReact>
</div>
 </div>
  </div>
  )
}

export default MyHomeListingItem;