import React from 'react';
import poster from '../images/poster.jpg';
import '../App.css';



const Poster=()=>{
  return (
    <div>
    <section className="mx-4 my-1 px-9 flex justify-center items-center">
    <img className=" poster max-w-xs md:max-w-xl" src={poster} alt="ok"/>
  </section>
    </div>
  )
}

export default Poster