import React,{useState} from 'react';
import NavBar from '../components/NavBar';
import { NavLink,useNavigate } from 'react-router-dom';
import '../App.css';

const SignUp =()=>{
    const [number , setNumber] = useState('');
    const [name,setName] = useState('');
    const [touched , setTouched] = useState('');
    const isValid = (number.length === 10);

    const [password1 , setPassword1] = useState('');
    const [password2 , setPassword2] = useState('');
    const [touchedPass1 , setTouchedPass1] = useState('');
    const [touchedPass2 , setTouchedPass2] = useState('');
    // const[passOk,setPassOK]= useState(false);
    let passOk;
    const isValidPass1 = (password1.length >= 8);
    const isValidPass2 = (password2.length >= 8);
    const navigate = useNavigate();

    const handleNumberChange=(evnt)=>{
        setNumber(evnt.target.value);
    }

    const handlePassChange1 =(evnt)=>{
        setPassword1(evnt.target.value);
    }

    const handlePassChange2 =(evnt)=>{
        setPassword2(evnt.target.value);

    }
    const handleNameChange = (evnt)=>{
        setName(evnt.target.value);
    }
    const submitHandler = (event)=>{
event.preventDefault();
passOk = password1===password2;
if(number.length===10 && password1.length>=8 && password2.length>=8 && password1===password2 && name.length>=1){
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ 
            "name":name,
            "number":number,
            "password":password2
         })
    };
    fetch('https://noagent4.onrender.com/users/signup', requestOptions)
        .then(response => response.json())
        .then(data => {
            if(data.code===1){
                navigate('/signin');

            }
        });

    


}

    }


  return (
    <div>
    <NavBar/>
  <div className=" signup relative flex flex-col justify-center min-h-screen overflow-hidden">
  <div className="w-full p-6 m-auto bg-white rounded-md shadow-md lg:max-w-xl">
      <h1 className="text-3xl font-semibold text-center text-purple-700 underline">
         Sign up
      </h1>
      <form onSubmit={submitHandler} className="mt-6">
          <div className="mb-2">
              <label
                  for="number"
                  className="block text-sm font-semibold text-gray-800"
              >
                  Number
              </label>
              <input
                  placeholder="Number"
                  value={number}
                  onChange={handleNumberChange}
                  onBlur={() => setTouched(true)}
                  type="number"
                  className="block w-full px-4 py-2 mt-2 text-purple-700 bg-white border rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
              />
              {touched ? (isValid ? "" : <label
                    for="number"
                    className="block text-sm font-semibold text-red-800"
                >
                    invalid number
                </label>) : null}
          </div>

          <div className="mb-2">
              <label
                  for="number"
                  className="block text-sm font-semibold text-gray-800"
              >
                  Name
              </label>
              <input
                  placeholder="name"
                  value={name}
                  onChange={handleNameChange}
                  type="text"
                  className="block w-full px-4 py-2 mt-2 text-purple-700 bg-white border rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
              />
            
          </div>


          <div className="mb-2">
              <label
                  for="password"
                  className="block text-sm font-semibold text-gray-800"
              >
                  Password
              </label>
              <input
                  onBlur={() => setTouchedPass1(true)}
                  onChange={handlePassChange1}
                  type="password"
                  className="block w-full px-4 py-2 mt-2 text-purple-700 bg-white border rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
              />

{touchedPass1 ? (isValidPass1 ? "" : <label
                    for="number"
                    className="block text-sm font-semibold text-red-800"
                >
                    password must be 8 characters long
                </label>) : null}



                <label
                  for="password"
                  className="block text-sm font-semibold text-gray-800"
              >
                  Confirm Password
              </label>
                <input
                  onBlur={() => setTouchedPass2(true)}
                  onChange={handlePassChange2}
                  type="password"
                  className="block w-full px-4 py-2 mt-2 text-purple-700 bg-white border rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
              />

{touchedPass2 ? (isValidPass2 ? "" : <label
                    for="number"
                    className="block text-sm font-semibold text-red-800"
                >
                    password must be 8 characters long
                </label>) : null}

                {passOk ? "":<label
                    for="number"
                    className="block text-sm font-semibold text-red-800"
                >
                    Both password must match each other.
                </label>}
          </div>
          {/* <a
              href="#"
              className="text-xs text-purple-600 hover:underline"
          >
              Forget Password?
          </a> */}
          <div className="mt-6">
              <button className="w-full px-4 py-2 tracking-wide text-white transition-colors duration-200 transform bg-blue-700 rounded-md hover:bg-blue-800 focus:outline-none focus:bg-blue-800">
                 Sign Up
              </button>
          </div>
      </form>

      <p className="mt-8 text-xs font-light text-center text-gray-700">
          {" "}
          Already have an account?{" "}
          <NavLink to ="/signin"
          
              className="font-medium text-purple-600 hover:underline"
          >
              Sign in
         </NavLink>
      </p>
  </div>
</div>
</div>
  )
}

export default SignUp;