import React, { useState,useContext } from 'react';
import '../App.css';
import NavBar from '../components/NavBar';
import { NavLink ,useNavigate} from 'react-router-dom';
import AuthContext from '../store/auth-context';


const SignIn =()=>{

  const ctx = useContext(AuthContext);


    const [number , setNumber] = useState('');
    const [touched , setTouched] = useState('');
    const isValid = (number.length === 10);

    const [password , setPassword] = useState('');
    const [touchedPass , setTouchedPass] = useState('');
    // const [name,setName] = useState('');
    const isValidPass = (password.length >= 8);
    const navigate = useNavigate();
    

    const handleNumberChange=(evnt)=>{
        setNumber(evnt.target.value);
    }

    // const handleNameChange=(evnt)=>{
    //     setName(evnt.target.value);
    // }


    const handlePassChange =(evnt)=>{
        setPassword(evnt.target.value);
    }
    
    const submitHandler = async(event)=>{
event.preventDefault();
const requestOptions = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ 
        "number":number,
        "password":password

     })
};
const response = await fetch('https://noagent4.onrender.com/users/login', requestOptions);
const data = await response.json();
if(data.token){
    ctx.NoAgentToken = "Bearer "+data.token;
    ctx.userId=data.user[0]._id;
    ctx.name = data.user[0].name;
    // ctx.onLogin();
    // localStorage.setItem('NoAgentToken',"Bearer "+data.token)
    navigate('/');
// console.log(data.user[0]._id);
}
    }

  return (
    <div>
      <NavBar/>
    <div className="relative flex flex-col justify-center min-h-screen overflow-hidden">
    <div className="w-full p-6 m-auto bg-white rounded-md shadow-md lg:max-w-xl">
        <h1 className="text-3xl font-semibold text-center text-purple-700 underline">
           Sign in
        </h1>
        <form onSubmit={submitHandler} className="mt-6">
            <div className="mb-2">
                <label
                    for="number"
                    className="block text-sm font-semibold text-gray-800"
                >
                    Number
                </label>
                <input
                    type="number"
                    placeholder="Number"
                    value={number}
                    onChange={handleNumberChange}
                    onBlur={() => setTouched(true)}
                    className="block w-full px-4 py-2 mt-2 text-purple-700 bg-white border rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                />
{touched ? (isValid ? "" : <label
                    for="number"
                    className="block text-sm font-semibold text-red-800"
                >
                    invalid number
                </label>) : null}
                
            </div>

            {/* <div className="mb-2">
              <label
                  for="number"
                  className="block text-sm font-semibold text-gray-800"
              >
                  Name
              </label>
              <input
                  placeholder="name"
                  value={name}
                  onChange={handleNameChange}
                  type="text"
                  className="block w-full px-4 py-2 mt-2 text-purple-700 bg-white border rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
              />
            
          </div> */}
            <div className="mb-2">
                <label
                    for="password"
                    className="block text-sm font-semibold text-gray-800"
                >
                    Password
                </label>
                <input
                onBlur={() => setTouchedPass(true)}
                onChange={handlePassChange}
                    type="password"
                    className="block w-full px-4 py-2 mt-2 text-purple-700 bg-white border rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                />

{touchedPass ? (isValidPass ? "" : <label
                    for="number"
                    className="block text-sm font-semibold text-red-800"
                >
                    password must be 8 characters long
                </label>) : null}
            </div>
            <a
                href="www.ok.com"
                className="text-xs text-purple-600 hover:underline"
            >
                Forget Password?
            </a>
            <div className="mt-6">
                <button className="w-full px-4 py-2 tracking-wide text-white transition-colors duration-200 transform bg-blue-700 rounded-md hover:bg-blue-800 focus:outline-none focus:bg-blue-800">
                    Login
                </button>
            </div>
        </form>

        <p className="mt-8 text-xs font-light text-center text-gray-700">
            {" "}
            Don't have an account?{" "}
            <NavLink to ="/signup"
             
                className="font-medium text-purple-600 hover:underline"
            >
                Sign up
            </NavLink>
        </p>
    </div>
</div>
</div>
  )
}


export default SignIn;