import React,{ useEffect,useState,useContext} from 'react';
import AuthContext from '../store/auth-context';
// import Poster from '../images/poster.jpg';
import { NavLink ,useNavigate} from 'react-router-dom';
import NavBar from '../components/NavBar';

const MyListings=(props)=>{
    const ctx = useContext(AuthContext);
const navigate = useNavigate();

    const [data,setData]=useState([]);


 
   
    
    useEffect(() => {
 
        if(!ctx.NoAgentToken){
        navigate('/');
        }


        fetch('https://noagent4.onrender.com/rent?user_id='+ctx.userId)
        .then(response => response.json())
        
        .then((res) => {
            setData(res.data.rent);
        })

   
    
       
},[]);
       
      


  return (
<div>
    








<NavBar/>




{!data.length>=1 && <div class="text-center mt-40">No Rent Listings</div>}


{/* <p class="text-center mt-8 border-b-4 border-indigo-600 font-bold">My Rent Listings</p> */}
  <div class="flex flex-col mt-2 justify-center items-center md:flex-wrap md:space-x-6 space-y-4 md:flex-row mt-16  ">

{data.map(data=>{
    return( 
  
    <div class="w-80 md:mt-4 border-4  border-blue-500 rounded-xl overflow-hidden hover:drop-shadow-lg">
       
<NavLink to={'/my-listing/'+data._id}>

        <div class="h-40 ">
          <img src={data.productImage[0]} class="h-[100%] w-[100%] object-cover" alt="listing"/>
        </div>
        <div class="textcontent mx-2 my-3 flex flex-col space-y-2">
        <p class="px-3 py-1 inline bg-gray-400 rounded-md">{data.room_type} </p>

        <p class=" ">{data.description}</p>
        <p class=" px-3 py-1 bg-blue-200 rounded-md">{data.district}-{data.municipality}-{data.ward}-{data.path} </p>
        <p class="px-3 py-1 inline bg-gray-400 rounded-md">Nrs:-{data.price} </p>
       
      </div>
</NavLink>
{/* <div class="px-3 py-1  bg-gray-300 rounded-md font-bold text-blue-700 flex justify-center" >
            <button class="px-3 py-1  bg-gray-300 rounded-md font-bold text-blue-700">Delete</button>
            <button class="px-3 py-1  bg-gray-300 rounded-md font-bold text-blue-700">View</button>
            <button class="px-3 py-1  bg-gray-300 rounded-md font-bold text-blue-700">Update</button>

        </div> */}
    </div>
  
    )      
})
}
</div>
</div>
 
  )
}

export default MyListings