import React, { useState } from 'react';

const AuthContext = React.createContext({
  NoAgentToken:'',
  userId:'',
  name:'',

  RentQuery:null,
  HomeQuery:null,
  LandQuery:null,

  showRentListings:false,
  isLoggedIn: false,
  onLogout: () => {},
  onLogin: (email, password) => {},
  showRent:()=>{},
  hideRent:()=>{}

});

export const AuthContextProvider = (props) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [NoAgentToken,setNoAgentToken]=useState('');
  // const [rentResponse,setRentResponse]= useState(false);
  const [showRentListings,setShowRentListings]= useState(false);
  const [userId,setUserId]= useState('');
  const [name,setName] = useState('');
  const [RentQuery,setRentQuery] = useState(null);
  const [HomeQuery,setHomeQuery] = useState(null);
  const [LandQuery,setLandQuery] = useState(null);

  




  const logoutHandler = () => {
setIsLoggedIn(false);
setNoAgentToken(null);
setUserId('');
setName('');
setRentQuery(null);
setHomeQuery(null);
setLandQuery(null);

// localStorage.setItem('NoAgentToken',null);



  };

  const showRentHandler = () => {
 setShowRentListings(true);
    
    
      };

      const hideRentHandler = () => {
        setShowRentListings(false);
           
           
             };

const loginHandler = ()=>{
localStorage.setItem('NoAgentToken',NoAgentToken);
}

  return (
    <AuthContext.Provider
      value={{
        NoAgentToken:NoAgentToken,
        isLoggedIn: isLoggedIn,
        showRentListings:showRentListings,
        userId:userId,
        name:name,
        RentQuery:RentQuery,
        HomeQuery:HomeQuery,
        LandQuery:LandQuery,
        onLogin:loginHandler,
        onLogout: logoutHandler,
        showRent:showRentHandler,
        hideRent:hideRentHandler
       
      }}
    >
      {props.children}
    </AuthContext.Provider>
  );
};

export default AuthContext;
